ndex';\n@import '../../style/mixins/index';\n@import './mixin';\n\n// Grid system\n.@{ant-prefix}-row {\n  display: flex;\n  flex-flow: row wrap;\n\n  &::before,\n  &::after {\n    display: flex;\n  }\n\n  // No wrap of flex\n  &-no-wrap {\n    flex-wrap: nowrap;\n  }\n}\n\n// x\n.@{ant-prefix}-row-start {\n  justify-content: flex-start;\n}\n\n// x\n.@{ant-prefix}-row-center {\n  justify-content: center;\n}\n\n// x\n.@{ant-prefix}-row-end {\n  justify-content: flex-end;\n}\n\n// x\n.@{ant-prefix}-row-space-between {\n  justify-content: space-between;\n}\n\n// x\n.@{ant-prefix}-row-space-around {\n  justify-content: space-around;\n}\n\n// \n.@{ant-prefix}-row-top {\n  align-items: flex-start;\n}\n\n// \n.@{ant-prefix}-row-middle {\n  align-items: center;\n}\n\n// \n.@{ant-prefix}-row-bottom {\n  align-items: flex-end;\n}\n\n.@{ant-prefix}-col {\n  position: relative;\n  max-width: 100%;\n  // Prevent columns from collapsing when empty\n  min-height: 1px;\n}\n\n.make-grid();\n\n// Extra small grid\n//\n// Columns, offsets, pushes, and pulls for extra small devices like\n// smartphones.\n\n.make-grid(-xs);\n\n// Small grid\n//\n// Columns, offsets, pushes, and pulls for the small device range, from phones\n// to tablets.\n\n@media (min-width: @screen-sm-min) {\n  .make-grid(-sm);\n}\n\n// Medium grid\n//\n// Columns, offsets, pushes, and pulls for the desktop device range.\n\n@media (min-width: @screen-md-min) {\n  .make-grid(-md);\n}\n\n// Large grid\n//\n// Columns, offsets, pushes, and pulls for the large desktop device range.\n\n@media (min-width: @screen-lg-min) {\n  .make-grid(-lg);\n}\n\n// Extra Large grid\n//\n// Columns, offsets, pushes, and pulls for the full hd device range.\n\n@media (min-width: @screen-xl-min) {\n  .make-grid(-xl);\n}\n\n// Extra Extra Large grid\n//\n// Columns, offsets, pushes, and pulls for the full hd device range.\n\n@media (min-width: @screen-xxl-min) {\n  .make-grid(-xxl);\n}\n\n@import './rtl';\n","@import '../../style/mixins/index';\n\n// mixins for grid system\n// ------------------------\n\n.loop-grid-columns(@index, @class) when (@index > 0) {\n  .@{ant-prefix}-col@{class}-@{index} {\n    display: block;\n    flex: 0 0 percentage((@index / @grid-columns));\n    max-width: percentage((@index / @grid-columns));\n  }\n  .@{ant-prefix}-col@{class}-push-@{index} {\n    left: percentage((@index / @grid-columns));\n  }\n  .@{ant-prefix}-col@{class}-pull-@{index} {\n    right: percentage((@index / @grid-columns));\n  }\n  .@{ant-prefix}-col@{class}-offset-@{index} {\n    margin-left: percentage((@index / @grid-columns));\n  }\n  .@{ant-prefix}-col@{class}-order-@{index} {\n    order: @index;\n  }\n  .loop-grid-columns((@index - 1), @class);\n}\n\n.loop-grid-columns(@index, @class) when (@index = 0) {\n  .@{ant-prefix}-col@{class}-@{index} {\n    display: none;\n  }\n  .@{ant-prefix}-col-push-@{index} {\n    left: auto;\n  }\n  .@{ant-prefix}-col-pull-@{index} {\n    right: auto;\n  }\n  .@{ant-prefix}-col@{class}-push-@{index} {\n    left: auto;\n  }\n  .@{ant-prefix}-col@{class}-pull-@{index} {\n    right: auto;\n  }\n  .@{ant-prefix}-col@{class}-offset-@{index} {\n    margin-left: 0;\n  }\n  .@{ant-prefix}-col@{class}-order-@{index} {\n    order: 0;\n  }\n}\n\n.make-grid(@class: ~'') {\n  .loop-grid-columns(@grid-columns, @class);\n}\n","@import '../../style/themes/index';\n@import '../../style/mixins/index';\n\n.@{ant-prefix}-row {\n  &-rtl {\n    direction: rtl;\n  }\n}\n\n// mixin\n.loop-grid-columns(@index, @class) when (@index > 0) {\n  .@{ant-prefix}-col@{class}-push-@{index} {\n    // reset property in RTL direction\n    &.@{ant-prefix}-col-rtl {\n      right: percentage((@index / @grid-columns));\n      left: auto;\n    }\n  }\n\n  .@{ant-prefix}-col@{class}-pull-@{index} {\n    // reset property in RTL direction\n    &.@{ant-prefix}-col-rtl {\n      right: auto;\n      left: percentage((@index / @grid-columns));\n    }\n  }\n\n  .@{ant-prefix}-col@{class}-offset-@{index} {\n    // reset property in RTL direction\n    &.@{ant-prefix}-col-rtl {\n      margin-right: percentage((@index / @grid-columns));\n      margin-left: 0;\n    }\n  }\n}\n\n.loop-grid-columns(@index, @class) when (@index = 0) {\n  .@{ant-prefix}-col-push-@{index} {\n    // reset property in RTL direction\n    &.@{ant-prefix}-col-rtl {\n      right: auto;\n    }\n  }\n\n  .@{ant-prefix}-col-pull-@{index} {\n    &.@{ant-prefix}-col-rtl {\n      left: auto;\n    }\n  }\n\n  .@{ant-prefix}-col@{class}-push-@{index} {\n    &.@{ant-prefix}-col-rtl {\n      right: auto;\n    }\n  }\n\n  .@{ant-prefix}-col@{class}-pull-@{index} {\n    &.@{ant-prefix}-col-rtl {\n      left: auto;\n    }\n  }\n\n  .@{ant-prefix}-col@{class}-offset-@{index} {\n    &.@{ant-prefix}-col-rtl {\n      margin-right: 0;\n    }\n  }\n}\n"],"sourceRoot":""}]);
